@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
* {
  box-sizing: border-box;
}

input {
  all: unset;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  box-sizing: border-box;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.mTwitter-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.mTwitter-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.mTwitter-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}
.formBtn.addMargin{
  margin-top: 10px;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
  border: 0px;
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

/* AddTwitter */

.addTwitterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.addTwitterInput_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.addTwitterInput_input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.addTwitterInput_arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.addTwitterInput_label {
  color: #04aaff;
  cursor: pointer;
}

.addTwitterInput_label span {
  margin-right: 10px;
  font-size: 12px;
}

.addTwitterInput_file{
  opacity: 0;
}

.addTwitterForm_attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.addTwitterForm_attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.addTwitterForm_clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.addTwitterForm_clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* MadeTeitter */

.mTwitterEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.mTwitter {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.mTwitter h4 {
  font-size: 14px;
}

.mTwitter img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.mTwitter_actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.mTwitter_actions span {
  cursor: pointer;
}

.mTwitter_actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10px;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

/* Navigation */
.naviUl{
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.homeLink{
  margin-right: 10px;
}

.profileLink{
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
}

.displayName{
  margin-top: 10px;
}

/* Router */
.routerStyle{
  width: 100%;
  margin: 0 auto;
  margin-top: 80px;
  display: flex;
  justify-content: center;
}
